@import url(https://fonts.googleapis.com/css2?family=Electrolize&family=Fira+Code:wght@300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@charset 'utf-8';

:root {
	--color-background: #1a1b1a;
	--color-primary: #e31837;
	--color-secondary: #f0f0f7;

	font-size: 60%;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body,
#root {
	height: 100vh;
}

body {
	background: #1a1b1a;
	background: var(--color-background);

	--bodyPaddingTop: 10vh;
}

#root {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: calc(100vh - var(--bodyPaddingTop));
}

body,
input,
button,
textarea {
	font: 500 1.6rem Roboto;
}

.container {
	width: 90vw;
	max-width: 700px;
}

@media (min-width: 700px) {
	:root {
		font-size: 62.5%;
	}
}

header {
	position: fixed;
	width: 100vw;
	height: 10vh;
	left: 0px;
  top: 0px;
  z-index: 100;

  padding: 0 5%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  color: white;
  
  background: #000000;
  border-bottom: 2px solid #7209B7;
}

header .logo {
  position: relative;
  font-family: 'Electrolize', sans-serif;
  font-size: 24px;
  font-weight: 500;
}

header .logo b {
  color: #bf60ff;
}

header .logo img {
  position: absolute;
  top: 2px;
  width: 30px;

  margin-left: 5px;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  list-style: none;

  font-family: 'Roboto', sans-serif;
}

header nav ul li {
  letter-spacing: 2px;

  padding: 10px;
}

header nav ul li a {
  position: relative;

  text-decoration: none;

  color: white;

  padding-bottom: 10px;
}

header nav ul li a:after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) scaleX(0);
  transform-origin: 50% 50%;
  width: 100%;
  height: 4px;
  background: #7209b7;
  transition: transform 100ms;
}

header nav ul li a:hover:after {
  transform: translateX(-50%) scaleX(1);
}

.section-inicio {
  width: 100vw;
  height: 100vh;

  background: #000;
  color: white;
  font-family: Roboto;
}

.section-inicio .bloom {
  width: 100vw;
  height: 100vh;
  border-radius: 50%;

  background: radial-gradient(circle, rgba(114,9,183,0.25) 0%, rgba(12, 52, 163, 0.25) 100%);
  -webkit-filter: blur(100px);
          filter: blur(100px);
}

.particle-texts {
  position: absolute;
  width: 100%;
  top: var(--bodyPaddingTop);
  padding: 5%;
}

.particle-texts h1 {
  font-family: 'Fira Code', monospace;
  text-shadow: 0px 4px 10px rgba(0, 0, 0, 1);
  font-style: normal;
  font-weight: bold;
  font-size: 50px;
  line-height: 80px;
}

.particle-texts p {
  text-shadow: 0px 4px 5px rgba(0, 0, 0, 1);

  width: 50%;
  font-weight: 500;
  font-size: 30px;
  line-height: 40px;
}

.green-highlight {
  color: #40ff53;
}

.yellow-highlight {
  color: #ffef62;
}

.code-icon {
  position: absolute;
  top: 30vh;
  right: 5%;
  
  display: flex;
  align-items: center;
  justify-content: end;
}

.code-icon code {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 144px;
  line-height: 150px;

  background: linear-gradient(110deg, rgba(255, 255, 255, 0.6) 45%, #FFF 50%, rgba(255, 255, 255, 0.6) 55%);
  background-size: 200% auto;
  
  color: #000;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
  -webkit-animation: shine 5s linear infinite;
  
          animation: shine 5s linear infinite;
}

@-webkit-keyframes shine {
  to {
    background-position: 200% center;
  }
}

@keyframes shine {
  to {
    background-position: 200% center;
  }
}

